html,
body {
  background: #f1f1f1;
  font-family: roboto;
}

* {
  font-family: roboto;
  font-weight: 300;
}

* {
  position: relative;
}

.wrapper {
  width: 100%;
  height: 100vh;
}
.content-wrapper {
  display: flex;
  height: 93vh;
  justify-content: center;
  align-items: center;
  background: #f1f1f1;
  margin: 0 auto;
  padding: 0 2em;
}

.card {
  width: 480px;
  max-width: 100%;
  height: 15em;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  box-shadow: 2px 3px 23px rgba(0, 0, 0, 0.1);
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  margin-top: 4em;
  transition: 0.2s;
  text-align: center;
}

@media (max-width: 600px) {
  .card {
    width: 25em;
  }
}

@media (max-height: 823px) {
  .card {
    margin-top: 9em;
  }
}

.card__flip-card {
  position: absolute;
  top: 4%;
  right: 5%;
  cursor: pointer;
  color: #333;
}

.card__content--front {
  -webkit-animation: fadeInUp 0.5s;
  animation: fadeInUp 0.5s;
  padding: 2em;
  font-size: 32px;
  text-align: center;
}
.card__content--pinyin {
  font-size: 16px;
  font-style: italic;
  opacity: 1;
}
.card__content--nope {
  opacity: 0;
}

.card__content--back {
  -webkit-animation: fadeIn 0.6s;
  animation: fadeIn 0.6s;
  line-height: 1.5;
  font-size: 14px;
  padding: 2em;
}

.card-container__icon {
  position: fixed;
  z-index: 100;
  font-size: 1.5em;
  top: 0.5em;
  left: 94%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  cursor: pointer;
  color: #333;
}
.audio_container {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  opacity: 0;
  transition: 0.3s;
  left: 0;
  opacity: 1;
  margin-top: 1em;
}

.card__actions {
  position: absolute;
  bottom: 10%;
  display: flex;
  width: 100%;
  justify-content: center;
  opacity: 0;
  transition: 0.3s;
  left: 0;
}

.card__actions.active {
  opacity: 1;
}

.card__next-button {
  margin-left: 0.5em;
  cursor: pointer;
  padding: 0.5em 0.75em;
  background: #f1f1f1;
  color: #444;
  transition: 0.2s;
}

.card__next-button:hover {
  background: #d8d8d8;
}

.card__prev-button {
  margin-right: 0.5em;
  cursor: pointer;
  padding: 0.5em 0.75em;
  background: #f1f1f1;
  color: #444;
  transition: 0.2s;
}

.card__prev-button:hover {
  background: #d8d8d8;
}
.card-container__dot {
  margin: 0 1em;
}

.card-container__dots-wrapper {
  padding: 1em 0em;
  margin-top: 3em;
  text-align: center;
}

.card-container__dot {
  color: lightgray;
  cursor: pointer;
}

.card-container__dot.active {
  color: dodgerblue;
  transition: 0.5s;
}
